import React from 'react'
import ComTab from './components/com-tab/ComTab.jsx'

import { useTranslation } from "react-i18next";
export default function Network (props) {
  const { t } = useTranslation();
  const menus = [
    {
      title: t('network.elastic_ip'),
      to: 'elastic_ip'
    },
    {
      title: t('cloud.security_group'),
      to: 'security_group'
    },
    {
      title: 'VPC',
      to: 'vpc'
    },
  ]
  return (
    <>
      <ComTab menus={menus}></ComTab >
    </>
  )
}
